<script>
  let stepSize = 50;
  let moveBX = 170;
  let moveJX = 340;
  let moveBY = 140;
  let moveJY = 140;
  let moveJasperX = 500;
  let moveJasperY = 165;
  let showJasper = false;
  let moveRixtX = 80;
  let moveRixtY = 165;
  let showRixt = false;
  let moveRedMonsterX = 300;
  let moveRedMonsterY = 80;
  let redMonster = false;
  let moveBlueMonsterX = 150;
  let moveBlueMonsterY = 80;
  let blueMonster = false;
  let moveMonsterYDown = 80;
  let moveMonsterYUp = 60;
  let moveYDown = 140;
  let moveYUp = 130;
  let moveUp = true;
  let scene = -1;
  let intermission = false;
  let bruid = true;
  let bruidegom = true;
  let showHelpJasper = false;
  let showCallJasper = false;
  let showHelpRixt = false;
  let showCallRixt = false;
  let showRedStar = false;
  let showBlueStar = false;
  let monsterRotate = 0;
  let punch = false;
  let final = false;

  let slaButton = false;

  const nextScene = () => {
    scene += 1;
    intermission = true;
    setTimeout(() => {
      intermission = false;
    }, 300);

    if (scene == 0) {
      moveBX = 170;
      moveJX = 340;
    } else {
      moveBX = 50;
      moveJX = 500;
    }

    if (scene == 2) {
      setTimeout(() => {
        showHelpJasper = true;
        showHelpRixt = true;
      }, 300);

      setTimeout(() => {
        showCallJasper = true;
        showCallRixt = true;
      }, 1000);

      setTimeout(() => {
        showHelpJasper = false;
        showHelpRixt = false;
      }, 1700);

      setTimeout(() => {
        showCallJasper = false;
        showCallRixt = false;
      }, 2500);

      if (bruid) {
        showRixt = false;
        showJasper = true;
        redMonster = true;
      } else if (bruidegom) {
        showRixt = true;
        showJasper = false;
        blueMonster = true;
      }
    }

    if (scene == 3) {
      console.log(scene);
      showRixt = true;
      showJasper = true;
      moveJasperX = 450;

      final = true;
      if (bruid) {
        moveJX = 260;
      } else {
        moveBX = 250;
      }
    }
  };

  const previousScene = () => {
    scene -= 1;
    intermission = true;
    setTimeout(() => {
      intermission = false;
    }, 300);

    if (scene == 0) {
      moveBX = 170;
      moveJX = 340;
    } else {
      moveBX = 500;
      moveJX = 50;
    }
  };

  $: if ((scene == 1 || scene == 2) && bruid && moveBX > moveJasperX) {
    nextScene();
  }

  $: if ((scene == 1 || scene == 2) && bruidegom && moveJX < moveRixtX) {
    nextScene();
  }
  // $: if (scene == 2) {
  //   if (bruid) {
  //     showRixt = false;
  //     showJasper = true;
  //     redMonster = true;
  //   } else if (bruidegom) {
  //     showRixt = true;
  //     showJasper = false;
  //     blueMonster = true;
  //   }
  // }

  $: if (redMonster) {
    console.log("showRedMonster");
    // if (moveBX > moveRedMonsterX - 20) {
    //   moveBX = moveRedMonsterX - 20;
    // }
  }

  const selectBernadet = () => {
    bruid = true;
    bruidegom = false;
    nextScene();
  };

  const selectJelmer = () => {
    bruid = false;
    bruidegom = true;
    nextScene();
  };
  let moveLinks = () => {
    if (bruid) {
      moveBX -= stepSize;
    }
    if (bruidegom) {
      moveJX -= stepSize;
      if (blueMonster && moveJX < moveBlueMonsterX + 80) {
        moveJX = moveBlueMonsterX + 80;
        slaButton = true;
      } else if (scene == 3 && moveJX < 270) {
        moveJX = 260;
      }
    }

    stepUp();
    stepUpMonster();
  };

  const stepUp = () => {
    if (bruid) {
      moveBY = moveYUp;
    }

    if (bruidegom) {
      moveJY = moveYUp;
    }

    setTimeout(() => {
      moveJY = moveYDown;
      moveBY = moveYDown;
    }, 100);
  };

  const stepUpMonster = () => {
    // moveRedMonsterY = moveMonsterYUp;
    setTimeout(() => {
      moveRedMonsterY = moveMonsterYUp;
      moveBlueMonsterY = moveMonsterYUp;
    }, 1000);

    setTimeout(() => {
      moveRedMonsterY = moveMonsterYDown;
      moveBlueMonsterY = moveMonsterYDown;
    }, 1100);
  };
  $: console.log(moveRedMonsterY);
  let moveRechts = () => {
    if (bruid) {
      moveBX += stepSize;
      if (redMonster && moveBX > moveRedMonsterX - 15) {
        moveBX = moveRedMonsterX - 15;
        slaButton = true;
      } else if (scene == 3 && moveBX > 240) {
        moveBX = 250;
      }
    }

    if (bruidegom) {
      moveJX += stepSize;
    }
    stepUp();
    stepUpMonster();
  };
  let action = () => {
    punch = true;

    setTimeout(() => {
      punch = false;
    }, 350);
  };
  let slaMonster = () => {
    slaButton = false;
    punch = true;

    setTimeout(() => {
      punch = false;
    }, 350);

    setTimeout(() => {
      monsterRotate = -15;
      moveRedMonsterY = moveMonsterYUp - 60;
      moveBlueMonsterX = moveBlueMonsterX - 40;
      moveBlueMonsterY = moveMonsterYUp - 60;
    }, 150);
    setTimeout(() => {
      if (bruid) {
        showRedStar = true;
      } else {
        showBlueStar = true;
      }
    }, 700);
    setTimeout(() => {
      redMonster = false;
      blueMonster = false;
    }, 900);
    setTimeout(() => {
      showRedStar = false;
      showBlueStar = false;
    }, 1100);
    console.log("slaMonster");
  };
</script>

